<template>
    <div>
        <el-main>
            <div class="select">
                <el-input v-model="id" placeholder="ID" clearable></el-input>
                <el-input v-model="name" placeholder="登陆名" clearable></el-input>
                <el-select v-model="roles" clearable placeholder="角色">
                    <el-option label="超级管理员" value="shanghai"></el-option>
                    <el-option label="外交部管理员" value="beijing"></el-option>
                    <el-option label="超级管理员" value="shanghai"></el-option>
                    <el-option label="教育部管理员" value="beijing"></el-option>
                </el-select>
                <el-button type="primary" icon="el-icon-search" class="search"></el-button>
            </div>
            <div class="btn">
                <el-button type="danger" icon="el-icon-delete-solid">批量删除</el-button>
                <el-button type="primary" icon="el-icon-document" @click="exportExcel()">导出</el-button>
            </div>
            <el-table
                id="out-table"
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'id', order: 'descending'}"
                :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
            >
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="id" label="ID" sortable></el-table-column>
                <el-table-column prop="name" label="登陆名"></el-table-column>
                <el-table-column prop="role" label="角色"></el-table-column>
                <el-table-column
                    prop="state"
                    label="状态"
                    min-width="100"
                    :filters="[{ text: '已启用', value: true }, { text: '已停用', value: false }]"
                    :filter-method="filterState"
                    filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.state === true ? 'primary' : 'warning'"
                            disable-transitions
                        >{{scope.row.state==true?'已启用':'已停用'}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                style="float: right;margin-top:8px;"
                background
                layout="prev, pager, next"
                :total="1000"
            ></el-pagination>
        </el-main>
        <pm :pmitem="pmitem" :pmVisible="pmVisible" @close-dialogStatus="Close_dialog"></pm>
    </div>
</template>

<script>
import pm from "@/components/dialog/pm";
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
    data() {
        return {
            pmitem: {},
            pmVisible: false,
            ip: "",
            time: "",
            name: "",
            roles: "",
            tableData: [
                {
                    id: "12345",
                    name: "admin",
                    role: "超级管理员",
                    state: true,
                    power: ""
                },
                {
                    id: "233333",
                    name: "kkk",
                    role: "国防部管理员",
                    state: true,
                    power: ""
                },
                {
                    id: "666666",
                    name: "aaa",
                    role: "教育部管理员",
                    state: true,
                    power: ""
                },
                {
                    id: "99999",
                    name: "ccccc",
                    role: "高级管理员",
                    state: true,
                    power: ""
                },
                {
                    id: "1564586845",
                    name: "CSfdsgfs",
                    role: "外交部管理员",
                    state: true,
                    power: ""
                },
                {
                    id: "43676587",
                    name: "eftrghy",
                    role: "国防部管理员",
                    state: false,
                    power: ""
                }
            ]
        };
    },
    components: {
        pm
    },
    methods: {
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        Close_dialog(val) {
            this.pmVisible = false;
        },
        handleEdit(index, row) {
            this.pmitem = row;
            this.pmVisible = true;
        },
        handleDelete(index, row) {
            this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$message({
                    type: "success",
                    message: "删除成功!"
                });
                for (let i in this.tableData) {
                    if (this.tableData[i] == row) {
                        this.tableData.splice(i, 1);
                    }
                }
            });
        },
        exportExcel() {
            var wb = XLSX.utils.table_to_book(
                document.querySelector("#out-table")
            );
            var wbout = XLSX.write(wb, {
                bookType: "xlsx",
                bookSST: true,
                type: "array"
            });
            try {
                FileSaver.saveAs(
                    new Blob([wbout], { type: "application/octet-stream" }),
                    "mlist.xlsx"
                );
            } catch (e) {
                if (typeof console !== "undefined") console.log(e, wbout);
            }
            return wbout;
        }
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.btn {
    margin-top: 10px;
    margin-bottom: 10px;
}

.select {
    display: inline;
}

.select, .el-input {
    width: 156px;
}

.search {
    position: relative;
    top: -1px;
}
</style>


