<template>
    <div>
        <el-dialog title="编辑" :visible.sync="dialogpmisible" @close="closeDialog" width="20%">
            <el-form ref="form" :model="info">
                <el-form-item label="角色">
                    <el-select v-model="info.role">
                        <el-option label="超级管理员" value="shanghai"></el-option>
                        <el-option label="外交部管理员" value="beijing"></el-option>
                        <el-option label="超级管理员" value="shanghai"></el-option>
                        <el-option label="教育部管理员" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="状态">
                    <el-switch v-model="info.state"></el-switch>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">提交</el-button>
                    <el-button @click="dialogpmisible=false;">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            info: {},
            dialogpmisible: false
        };
    },
    props: {
        pmVisible: Boolean,
        pmitem: Object
    },
    watch: {
        pmVisible(newValue, oldValue) {
            this.dialogpmisible = newValue;
        },
        pmitem(newValue, oldValue) {
            this.info = newValue;
        }
    },
    methods: {
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        },
        onSubmit() {
            this.$message({
                message: "修改成功",
                type: "success"
            });
            this.dialogpmisible = false;
        }
    }
};
</script>


